footer {
  display: block;
  background-color: #f06220;
}

.container-fluid {
  text-align: center;
}

.row {
  justify-content: space-around;
  align-items: baseline;
  padding: 20px;
}

.row-items {
  margin: 5px;
}

.socialMedias {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
}

.socialMedias a {
  margin: 0 5px;
  font-size: 30px;
}

.socialMedias a:hover {
  color: white;
}

h5 {
  font-weight: bold;
}

h6 {
  color: black;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: black;
}

a:visited {
  cursor: pointer;
  text-decoration: none;
  color: black;
}

a:hover {
  cursor: pointer;
  text-decoration: none;
  color: black;
}
